.сompanyNav {
  display: flex;
  width: calc(100% - 150px);
}
.overflowClass {
  overflow: hidden;
  width: 100%;
}
.companyNavBlock {
  z-index: 1;
  display: -ms-flexbox;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 110px;
  max-width: 150px;
  height: 54px;
  border: 2px solid #c4c4c4;
