.navContainerMap {
  position: relative;
  display: flex;
  max-width: 950px;
  margin: 0 auto;
  justify-content: space-between;
  top: 50px;
}
.gm-fullscreen-control {
  display: none;
}
.mapContainer {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100vh;
}
.mapMarkerWrap {
  position: absolute;
  top: -90px;
  left: -38px;
  cursor: pointer;
  width: 100px;
  background-color: transparent;
  border-radius: 5px;
  overflow: hidden;
}
.mapNoTranslationWrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.mapNoTranslationText {
  color: #fff;
  text-align: center;
  padding: 3px;
}
.GooggleMapReact {
  height: 100%;
  width: 100%;
}
.mapMarker {
  width: 100%;
  height: 85px;
  background-color: black;
  display: flex;
  align-items: center;
}
.mapMarkerName {
  padding: 1px 2px;
  font-size: 10px;
  height: 30px;
  width: 100%;
  text-align: center;
  font-weight: 500;
  border-radius: 0 0 5px 5px;
  background-color: #fff;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  box-shadow: 0 0px 7px 0px rgba(0, 0, 0, 0.3);
}
.openedTo {
  display: block;
  font-size: 9px;
  font-weight: 400;
  white-space: nowrap;
  overflow: hidden;
  line-height: 13px;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
}
.markerText {
  color: #fff;
  font-size: 9px;
  text-align: center;
  padding: 5px;
}
.arrow {
  width: 0;
  height: 0;
  margin: 0 auto;
  border: 10px solid transparent;
  border-top-color: #fff;
  border-bottom: 0;
}
.mapMarker img {
  width: 100%;
  height: 100%;
}
.clusterMarker {
  width: 36px;
  height: 36px;
  background: #e32a6c;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.clusterMarker p {
  font-weight: 700;
  font-size: 16px;
  color: #ffffff;
}
@media screen and (max-width: 1020px) {
  .navContainerMap {
    padding: 0 25px;
    transition: 0.2s ease all;
  }
}
@media screen and (max-width: 760px) {
  .navContainerMap {
    justify-content: center;
  }
  .mapContainer {
    position: fixed;
    top: 105px;
    width: 100%;
    height: calc(100% - 100px);
  }
}
